import React, { useState, useEffect } from 'react'
import './PictureBanner.css'

export const PictureBanner = ({page}) => {

    const [pictures, setPictures] = useState(null)

    const getPictures = async () => {
        console.log("Running Edit Post")
        try {
            let res = await fetch(`posts/getPictures`, {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    page: page,
                })
            })
    
            let result = await res.json();
            console.log(result)
            setPictures(result.posts)
        }
    
        catch (e) {
            console.log(e)
        }
       }

    useEffect(() => {
        getPictures()
      },[])

    return (
      <div className="row" style={{backgroundImage: `url('/${page}BannerBack.jpg')`}}> 

      {pictures ? pictures.map(picture => <div className="column">
    <img src={picture.image} style={{width:"100%"}} />
  </div> ) : ""}

  {/* <div className="column">
    <img src={url} style={{width:"100%"}} />
  </div>
  <div className="column">
    <img src={url} style={{width:"100%"}} />
  </div>
  <div className="column">
    <img src={url} style={{width:"100%"}} />
  </div>
  <div className="column">
    <img src={url} style={{width:"100%"}} />
  </div> */}
</div>
    )
}